.brand{
    position: relative;
    height: 700px;
    font-weight: 480;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
.brand::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('./../images/Brand\ Background\ Image.jpeg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: brightness(50%);
    z-index: -1;
}